<template>
  <v-navigation-drawer
    id="core-navigation-drawer"
    :right="$vuetify.rtl"
    app
    permanent
    stateless
    touchless
    v-bind="$attrs"
    width="260"
  >
    <v-card
      flat
      tile
    >
      <div>
        <v-menu
          v-model="showLang"
          bottom
          offset-y
        >
          <template #activator="{attrs, on}">
            <v-btn
              block
              style="height: 46px;border-bottom: 0.5px solid lightgray"
              text
              tile
              v-bind="attrs"
              v-on="on"
            >
              <v-icon left>
                mdi-web
              </v-icon>
              {{ $t('语言') }}
            </v-btn>
          </template>

          <v-list
            :tile="false"
            nav
          >
            <v-list-item
              v-for="(n, i) in notifications"
              :key="i"
              @click="changeLanguage(n)"
            >
              <v-list-item-title
                v-text="n"
              />
            </v-list-item>
          </v-list>
        </v-menu>
      </div>
      <!--      <div>-->
      <!--        <v-card-->
      <!--          class="d-flex justify-center align-center pl-1"-->
      <!--          height="46px"-->
      <!--          dark-->
      <!--          elevation="0"-->
      <!--          tile-->
      <!--          @click="showLogout=true"-->
      <!--        >-->
      <!--          <v-icon left>-->
      <!--            mdi-home-export-outline-->
      <!--          </v-icon>-->
      <!--          <span>{{ $t('logout') }}</span>-->
      <!--        </v-card>-->
      <!--      </div>-->
    </v-card>

    <div
      class="d-flex align-center"
      style="margin-bottom: 12px"
    >
      <div class="ml-4 mt-4">
        <v-img
          alt="John"
          src="@/assets/logonew.png"
          width="48"
        />
      </div>

      <div>
        <div class="text-overline">
          {{ $t('Aaden ADMIN') }}
        </div>
        <div>{{ Version }} Beta</div>
      </div>
    </div>

    <v-switch
      v-model="toggleBriefView"
      :label="$t('briefView')+': '+(toggleBriefView ? $t('on') : $t('off'))"
      class="pl-5 pb-2"
      hide-details
    />

    <v-divider class="mx-2"/>

    <v-list
      class="pb-16"
      expand
      nav
    >
      <v-list-item-group
        color="primary"
      >
        <template v-for="(firstItem,i) in computedItems">
          <template v-if="firstItem.children">
            <v-list-group
              :key="'firstItem'+i"
              :prepend-icon="firstItem.icon"
            >
              <template #activator>
                <v-list-item-title class="text-h4">
                  {{ firstItem.title }}
                </v-list-item-title>
              </template>

              <template v-for="(secondItem,j) in firstItem.children">
                <template v-if="secondItem.children">
                  <v-list-group
                    :key="'second'+j"
                    no-action
                    sub-group
                  >
                    <template #activator>
                      <v-list-item
                        class="text-h3"
                        dense
                      >
                        <v-list-item-icon class="my-2">
                          <v-icon small>
                            mdi-checkbox-blank-circle
                          </v-icon>
                        </v-list-item-icon>
                        <v-list-item-title class="text-h4">
                          {{ secondItem.title }}
                        </v-list-item-title>
                      </v-list-item>
                    </template>

                    <v-list-item
                      v-for="(thirdItem,k) in secondItem.children"
                      :key="'third'+k"
                      :to="secondItem.group+'/' +thirdItem.to"
                      class="pl-4"
                      dense
                    >
                      <v-list-item-icon class="my-2">
                        <v-icon small>
                          mdi-circle-medium
                        </v-icon>
                      </v-list-item-icon>
                      <v-list-item-title class="text-h4">
                        {{ thirdItem.title }}
                      </v-list-item-title>
                    </v-list-item>
                  </v-list-group>
                </template>

                <template v-else>
                  <v-list-item
                    :key="'second'+j"
                    :to="firstItem.group+'/' +secondItem.to"
                    class="pl-4 "
                    dense
                  >
                    <v-list-item-icon class="my-2">
                      <v-icon small>
                        mdi-checkbox-blank-circle-outline
                      </v-icon>
                    </v-list-item-icon>
                    <v-list-item-title class="text-h4">
                      {{ secondItem.title }}
                    </v-list-item-title>
                  </v-list-item>
                </template>
              </template>
            </v-list-group>
          </template>

          <template v-else>
            <v-list-item
              :key="'firstMenu'+i"
              :to="firstItem.to"
              class="py-1"
              dense
            >
              <v-list-item-icon class="my-2">
                <v-icon>{{ firstItem.icon }}</v-icon>
              </v-list-item-icon>
              <v-list-item-title class="text-h4">
                {{ firstItem.title }}
              </v-list-item-title>
            </v-list-item>
          </template>
        </template>
      </v-list-item-group>
    </v-list>

    <v-dialog
      v-model="showLogout"
      max-width="40vw"
    >
      <v-card height="20vh">
        <div class="d-flex justify-space-between flex-nowrap align-center">
          <div style="width: 125px">
            <v-img
              alt="John"
              src="@/assets/logonew.png"
            />
          </div>

          <div>
            <v-card-title class="text-h3 py-6">
              {{ $t('Are you sure?') }} {{ ' ' }}{{ $t('Do you want to logout?') }}
            </v-card-title>

            <v-card-actions
              class="fill-height"
              style="vertical-align: bottom"
            >
              <v-spacer/>
              <v-btn
                class="red white--text"
                to="/pages/login"
              >
                {{ $t('OK') }}
              </v-btn>
              <v-btn
                class="green white--text"
                @click="showLogout= false"
              >
                {{ $t('Cancel') }}
              </v-btn>
            </v-card-actions>
          </div>
        </div>
      </v-card>
    </v-dialog>
  </v-navigation-drawer>
</template>

<script>
  // Utilities
  import { mapMutations, mapState } from 'vuex'
  import { version } from '../../../../../package.json'
  // import { VHover, VListItem } from 'vuetify/lib'
  import { changeLanguage } from '@/GlobalSettings'

  export default {

    name: 'DashboardCoreDrawer',

    components: {},
    props: {
      expandOnHover: {
        type: Boolean,
        default: false,
      },
    },

    data: () => ({
      showLogout: false,
      showLang: false,
      menuRouter: [
        {
          icon: 'mdi-view-dashboard',
          title: 'dashboard',
          accessRight: 0,
          to: '/',
          brief: true,
        },
        {
          title: 'Zbon日历',
          accessRight: 1,
          icon: 'mdi-calendar',
          to: '/Bills/daily-zbon',
          brief: true,
        },
        {
          title: 'Zbon列表',
          accessRight: 1,
          icon: 'mdi-format-list-bulleted',
          to: '/Bills/zbonlist',
          brief: true,
        },
        {
          group: '/dishes',
          icon: 'mdi-food',
          title: '菜品',
          accessRight: 0,
          children: [
            {
              title: 'Category',
              icon: 'mdi-label',
              to: 'labels-manage',
              brief: true,
            },

            {
              title: '菜品',
              icon: 'mdi-food-fork-drink',
              to: 'dishes-manage',
              brief: true,
            },
            // {
            //   title: 'ConsumeTypeSetting',
            //   icon: 'mdi-calculator-variant',
            //   to: 'buffet-child-setting',
            // },
            {
              title: '属性',
              icon: 'mdi-calculator-variant',
              to: 'attribute-group',
              brief: true,
            },
            // {
            //   title: '属性组',
            //   icon: 'mdi-calculator-variant',
            //   to: 'attribute-group',
            // },

            {
              title: 'BuffetSetting',
              icon: 'mdi-buffet',
              to: 'buffet-setting',
              brief: true,
            },

            {
              group: '/dishes',
              title: '更多设置',
              accessRight: 2,
              icon: 'mdi-dots-horizontal-circle-outline',
              children: [
                {
                  title: 'BuffetChildSetting',
                  icon: 'mdi-buffet',
                  to: 'buffet-child-setting',
                },
                {
                  title: '折扣规则',
                  icon: 'mdi-help',
                  to: 'discount-rules-setting',
                },
                {
                  title: '折扣规则(新)',
                  icon: 'mdi-help',
                  to: 'discount-rules-newsetting',
                },
                // {
                //   title: '营业时间',
                //   icon: 'mdi-help',
                //   to: 'open-time-setting',
                // },
                {
                  title: '过敏源',
                  icon: 'mdi-help',
                  to: 'allergen-setting',
                },
                {
                  title: '库存',
                  icon: 'mdi-label',
                  to: 'cache-table',
                },
                // {
                //   title: '阈值颜色',
                //   icon: 'mdi-help',
                //   to: 'threshold-color-setting',
                // },
                {
                  title: 'Pfand',
                  icon: 'mdi-replay',
                  to: 'pfand',
                },

              ],
            },
          ],
        },

        {
          group: '/Tables',
          icon: 'mdi-desktop-mac',
          title: '桌子人员',
          accessRight: 1,
          children: [
            {
              title: '分区',
              icon: 'mdi-desktop-tower-monitor',
              to: 'section-manage',
            },
            {
              title: '桌子',
              icon: 'mdi-table-chair',
              to: 'tables-manage',
            },
            {
              title: '人员',
              icon: 'mdi-account-switch',
              to: 'person-manage',
            },
          ],
        },

        {
          group: '/Sales',
          icon: 'mdi-chart-line',
          title: '营销统计',
          accessRight: 1,
          children: [

            {
              title: '菜品统计',
              icon: 'mdi-calculator-variant',
              to: 'dish-statistics',
            },
            // {
            //   title: '销量分时统计',
            //   icon: 'mdi-plus',
            //   to: 'dish-eichtzeit-statistics',
            // },
            {
              title: '菜品出库统计',
              icon: 'mdi-calculator-variant',
              to: 'dish-delivery-statistics',
              brief: true,
            },
            {
              title: '高级销售分析',
              icon: 'mdi-calculator-variant',
              to: 'sales-insight',
            },
            {
              title: '代金券销售记录',
              icon: 'mdi-help',
              to: 'member-card-sale-record',
            },
            {
              title: 'CoronaInfo',
              icon: 'mdi-virus',
              to: 'corona-info',
            },

          ],
        },
        {
          group: '/Bills',
          icon: 'mdi-account-cash',
          title: '账单',
          accessRight: 1,
          children: [
            {
              title: '全部账单',
              icon: 'mdi-currency-usd',
              to: 'history-bills',
              brief: true,
            },
            {
              title: '导出CSV',
              icon: 'mdi-format-list-bulleted',
              to: 'exportcsv',
            },
            {
              title: '现金账',
              icon: 'mdi-format-list-bulleted',
              to: 'cashbook',
            },
            {
              title: 'Datev',
              icon: 'mdi-format-list-bulleted',
              to: 'Datev',
            },
          ],
        },
        {
          group: '/Printer',
          icon: 'mdi-printer',
          title: '打印',
          accessRight: 0,
          children: [
            {
              title: '操作记录',
              icon: 'mdi-developer-board',
              to: 'operationlist',
            },
            {
              title: '打印分区',
              icon: 'mdi-cog-transfer',
              to: 'section-printer-setting',
              brief: true,
            },
            {
              title: '设备列表',
              icon: 'mdi-devices',
              to: 'devicelist',
            },
            {
              title: '打印记录',
              icon: 'mdi-clipboard-list',
              to: 'printerlist',
              brief: true,
            },
            {
              title: 'BonTemplate',
              icon: 'mdi-clipboard-list',
              to: 'bon-template',
            },
            {
              title: '自由信息单',
              icon: 'mdi-text-box-outline',
              to: 'freeInfoSheet',
            },
          ],
        },
        {
          group: '/Setting',
          icon: 'mdi-account-cog',
          title: '设置',
          accessRight: 1,
          children: [

            {
              group: '/TakeawaySetting',
              title: '外卖',
              accessRight: 2,
              icon: 'mdi-dots-horizontal-circle-outline',
              children: [
                {
                  title: '配送距离',
                  icon: 'mdi-help',
                  to: 'delivery-range-setting',
                },
                {
                  title: '营业时间',
                  icon: 'mdi-help',
                  to: 'open-time-setting',
                },
                // {
                //   title: '用户信息',
                //   icon: 'mdi-help',
                //   to: 'user-management',
                // },
                // {
                //   title: '外卖设置',
                //   icon: 'mdi-plus',
                //   to: 'takeaway-setting',
                // },
              ],
            },
            {
              group: '/Setting',
              title: '图片',
              accessRight: 2,
              icon: 'mdi-image-area',
              children: [
                {
                  title: '奶茶图片',
                  icon: 'mdi-silver-ware',
                  to: 'restaurant-image',
                },
                {
                  title: 'K2轮播图',
                  icon: 'mdi-silver-ware',
                  to: 'banner-image',
                },
              ],
            },
            {
              title: '支付',
              icon: 'mdi-help',
              to: 'paymentpage',
            },
            // {
            //   title: '预约',
            //   icon: 'mdi-calendar-today',
            //   to: 'reservation',
            // },
            {
              title: '餐馆信息',
              icon: 'mdi-silver-ware',
              to: 'restaurant-info',
            },

            {
              title: '设置',
              icon: 'mdi-help',
              to: 'aaden-system-setting',
            },
            // {
            //   title: '提交问题',
            //   icon: 'mdi-help',
            //   to: 'q-and-a',
            // },
            {
              title: '关于我们',
              icon: 'mdi-account-group',
              to: 'about-us',
            },
          ],
        },
      ],
      Version: version,
      toggleBriefView: true,
      notifications: [
        'ZH',
        'EN',
        'DE',
      ],
      profile: [
        { title: 'logout' },
      ],
    }),

    computed: {
      ...mapState(['barColor', 'barImage', 'accessLevel', 'drawer']),
      drawer: {
        get () {
          return this.$store.state.drawer
        },
        set (val) {
          console.log(val)
          this.$store.commit('SET_DRAWER', val)
        },
      },
      computedItems () {
        const convertedItems = this.menuRouter.map(this.mapItem)
        // console.log('menuRouter', this.menuRouter)
        // const convertedItems = this.menuRouter

        if (this.toggleBriefView) {
          const briefItems = []

          for (let i = 0; i < convertedItems.length; i++) {
            // firstStepItem
            const firstStepItem = convertedItems[i]

            if (firstStepItem.brief !== undefined && firstStepItem.brief === true) {
              briefItems.push(firstStepItem)
            }

            // secondItem
            if (firstStepItem.children !== undefined && firstStepItem.children.length > 0) {
              const secondStepItem = firstStepItem.children
              const secondBriefItem = secondStepItem.filter(
                (child) => child.brief !== undefined && child.brief === true,
              )

              for (const child of secondBriefItem) {
                child.to = firstStepItem.group + '/' + child.to
              }

              briefItems.push(...secondBriefItem)

              // console.log('secondStepItem', secondStepItem)

              // console.log('secondStepItem.children1', secondStepItem.children)
              // if (secondStepItem.children !== undefined && secondStepItem.children.length > 0) {
              //   console.log('secondStepItem.children2', secondStepItem.children)
              for (let i = 0; i < secondStepItem.length; i++) {
                const thirdStepItem = secondStepItem[i]

                if (thirdStepItem.children !== undefined && thirdStepItem.children.length > 0) {
                  const thirdBriefItem = thirdStepItem.children.filter(
                    (child) => child.brief !== undefined && child.brief === true,
                  )

                  for (const child of thirdBriefItem) {
                    child.to = thirdStepItem.group + '/' + child.to
                  }

                  briefItems.push(...thirdBriefItem)
                }
              }

              // }
            }
          }
          return briefItems
        } else {
          return convertedItems
        }
      },
    },

    watch: {
      '$vuetify.breakpoint.smAndDown' (val) {
        this.$emit('update:expandOnHover', !val)
      },
    },

    methods: {
      changeLanguage,

      ...mapMutations({
        setDrawer: 'SET_DRAWER',
      }),

      mapItem (item) {
        return {
          ...item,
          children: item.children ? item.children.map(this.mapItem) : undefined,
          title: this.$t(item.title),
        }
      },
    },
  }
</script>

<style lang="sass">
@import '~vuetify/src/styles/tools/_rtl.sass'

#core-navigation-drawer
  &.v-navigation-drawer--mini-variant
    .v-list-item
      justify-content: flex-start !important

    .v-list-group--sub-group
      display: block !important

  .v-list-group__header.v-list-item--active:before
    opacity: .24

  .v-list-item
    &__icon--text,
    &__icon:first-child
      justify-content: center
      text-align: center
      width: 20px

      +ltr()
        margin-right: 24px
        margin-left: 12px !important

      +rtl()
        margin-left: 24px
        margin-right: 12px !important

  .v-list--dense
    .v-list-item
      &__icon--text,
      &__icon:first-child
        margin-top: 10px

  .v-list-group--sub-group
    .v-list-item
      +ltr()
        padding-left: 8px

      +rtl()
        padding-right: 8px

    .v-list-group__header
      +ltr()
        padding-right: 0

      +rtl()
        padding-right: 0

      .v-list-item__icon--text
        margin-top: 19px
        order: 0

      .v-list-group__header__prepend-icon
        order: 2

        +ltr()
          margin-right: 8px

        +rtl()
          margin-left: 8px
</style>
